/*账号资金左main页面*/
<template>
  <div class="mainCon accountCapital">
    <div class="mainTop">
      <div class="mainTopLeft">
				<el-button type="success" icon="el-icon-refresh" size="mini" @click="initData">刷新</el-button>
      </div>
      <div style="float: right;padding-top: 1vh;padding-right:1vh;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="50"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total"
        ></el-pagination>
        <div class='common' >共{{ total }}笔</div>
      </div>
      
    </div>
    <div class="mainBottom">
      <el-table
        v-loading="loading"
        :data="tableData"
        style="width: 100%"
        height="76vh"
        stripe
        border>
        <el-table-column
          fixed
          type="index"
          min-width="50">
        </el-table-column>
        <el-table-column
          prop="serial_number"
          fixed
          show-overflow-tooltip
          label="流水号"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="created_at"
          show-overflow-tooltip
          label="记账时间"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="money_lines"
          label="记账金额"
          show-overflow-tooltip
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="money_remaining"
          show-overflow-tooltip
          label="账户余额"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="user_id"
          show-overflow-tooltip
          label="用户"
          min-width="80">
        </el-table-column>
        <el-table-column
          prop="note"
          show-overflow-tooltip
          label="记账摘要/备注"
          min-width="300">
        </el-table-column>
      </el-table>
    </div>
    <div class="total">
      <p>入账总额：{{ ruzhuangSam }}  出账总额：{{ chuzhuangSam }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      loading: false,//加载状态
      currentPage: 1,//第几页
      pagerCount: 5,//按钮数量
      total: 0,//总数量
      ruzhuangSam: '',//入账总和
      chuzhuangSam: '',//出账总和
      // tableColumnState: false,//表格展开行显示状态
      //验证规则
      rules: {
        name: [ { required: true, message: '标题不能为空'} ],
      },
      //表格数据
      tableData: [],
      //多选列表
      multipleSelection: [],
      activeName: 'first',//标签页默认显示
    }
  },
   methods: {
     //改变页码
    handleSizeChange(val) {
      this.$store.commit('CHANGE_ACCOUNTCAPITAL_LIMIT',val);
    },
    //改变每页多少条
    handleCurrentChange(val) {
      this.$store.commit('CHANGE_ACCOUNTCAPITAL_PAGE',val);
    },
    //改变复选框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //初始化数据
    async initData(){
      this.loading = true;
      let res = await this.api.accountLists(this.$store.state.accountCapitalData);
        this.tableData = res.data.data.data;
        this.total = res.data.data.total;
        this.ruzhuangSam = res.data.data.ruzhuangSam;
        this.chuzhuangSam = res.data.data.chuzhuangSam;
      this.loading = false;
    }
  },
  beforeMount(){
    this.initData();
  },
  computed: {
    //每页多少条改变
    limit(){
      return this.$store.state.accountCapitalData.limit;
    },
    //页数改变
    page(){
      return this.$store.state.accountCapitalData.page;
    },
    //出账或入账状态
    type(){
      return this.$store.state.accountCapitalData.type;
    },
    //资金账户的时间
    time(){
      return this.$store.state.accountCapitalData.time;
    },
    //改变userid
    changeUserId(){
      return this.$store.state.changeUserId;
    }
  },
  watch: {
    limit: function(newVal,oldVal){
      this.initData();
    },
    page: function(newVal,oldVal){
      this.initData();
    },
    type: function(newVal,oldVal){
      this.initData();
    },
    time: function(newVal,oldVal){
      this.initData();
    },
    changeUserId: function(newVal,oldVal){
      this.initData(newVal);
    },
  }
}
</script>


