        activeName: 'first',
        tabPaneData: {},
        loading: false,
      };
    },
    methods: {
      //初始化数据
      async initData(){
        this.loading = true;
        let res = await this.api.personalPayment();
        this.tabPaneData = res.data.data;
        this.loading = false;
      }
    },
    created(){
      this.$nextTick(function(){
        this.initData()
      })
    }
  };
