/*订单管理管理top组件*/
<template>
  <div class="topCon">
    <div class="selectCon">
      <accountCom />
    </div>
    <div class="searchCon">
      <div class="selectTwoCon">
        <el-select v-model="value" placeholder="选择出入账" size="mini" @change="handleChange">
          <el-option
            v-for="item in accountList"
            :key="item.userId"
            :label="item.name"
            :value="item.userId">
          </el-option>
        </el-select>
      </div>
      <div class="dateTimeCon">
        <el-date-picker
        v-model="timeValue"
        type="daterange"
        align="right"
        unlink-panels
        format="MM-dd"
        range-separator="~"
        start-placeholder=""
        end-placeholder=""
        :picker-options="pickerOptions"
        size="mini"
        :clearable="false"
        @change="handleChangeDateTime"
        value-format="timestamp"
        >
        </el-date-picker>
      </div>
      <el-button class="btnStyle" size="mini" icon="el-icon-search">筛选</el-button>
    </div>
  </div>
</template>

<script>
  import accountCom from '@/components/OptionalCharges/accountCom'
export default {
  data(){
    return {
      inputValue: '',//输入框value
      checked: false,//全选状态
      accountList: [
        {
          name: '全部',
          userId: '',
        },{
          name: '出账',
          userId: '2',
        },{
          name: '入账',
          userId: '1',
        }
      ],
        value: '',
        timeValue: [ new Date().getTime() - 3600 * 1000 * 24 * 31, new Date() ],
        pickerOptions: {
          shortcuts: [
            {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '全部',
            onClick(picker) {
              const end = ``;
              const start = ``;
              picker.$emit('pick', start);
            }
          }]
        },
    }
  },
  methods: {
    //修改出入账状态
    handleChange(value){
      this.$store.commit('CHANGE_ACCOUNTCAPITAL_TYPE',value);
    },
    //修改时间
    handleChangeDateTime(dateTime){
      if(dateTime.length!=null&&dateTime!=''){
        let updateTime = dateTime.join(',');
        this.$store.commit('CHANGE_ACCOUNTCAPITAL_TIME',updateTime);
      }
      if(dateTime==''){
        this.$store.commit('CHANGE_ACCOUNTCAPITAL_TIME',dateTime);
      }
    }
  },
  components: {
    accountCom
  }
}
</script>

